class Game extends React.Component {
    constructor(props) {
        super(props);

        this.state = {x: 0, o: 0, xIsNow: true}
        this.handlePoint = this.handlePoint.bind(this);
        this.handleXOChange = this.handleXOChange.bind(this);
    }
    handlePoint(val) {
        (val === 'X') ? this.setState({x: ++(this.state.x)}) : this.setState({o: ++(this.state.o)});
        return val
    }
    handleXOChange() {
        this.setState({xIsNow: !this.state.xIsNow});
    }
    render() {
        return (
            <div className='game'>
                <Board onPoint={this.handlePoint} onXOChange={this.handleXOChange}/>
                <span className='count'>x: {this.state.x} o: {this.state.o}</span>
                <span className='playerNow'>{this.state.xIsNow ? 'X' : 'O'}</span>
            </div>);
    }
}

ReactDOM.render(
    <Game />,
    document.getElementById('root')
);

//=====================

function arrayObjGenerate(n) {
    let squareCount;
    if((n === 0) || (n === 1)) { squareCount = 0 }
    squareCount = 21 + ((32 + 4*(n-5))/2)*(n-4);
    let squaresArr = [];
    for (let i = 0; i < squareCount; i++) {
        if(i === 0 || i === 3 || i === 8 || i === 15 || i === 24 || i === 35 || i === 48) {
                squaresArr.push({
                    id: i,
                    value: null,
                    up: true,
                    right: false,
                    down: false,
                    left: true
                });
        } else if(i === 2 || i === 7 || i === 14 || i === 23 || i === 34 || i === 47 || i === 62){
            squaresArr.push({
                id: i,
                value: null,
                up: true,
                right: true,
                down: false,
                left: false
            });
        } else if(i === 78 || i === 93 || i === 106 || i === 117 || i === 126 || i === 133 || i === 138) {
            squaresArr.push({
                id: i,
                value: null,
                up: false,
                right: false,
                down: true,
                left: true
            });
        } else if(i === 92 || i === 105 || i === 116 || i === 125 || i === 132 || i === 137 || i === 140) {
            squaresArr.push({
                id: i,
                value: null,
                up: false,
                right: true,
                down: true,
                left: false
            });
        } else if(i === 1) {
            squaresArr.push({
                id: i,
                value: null,
                up: true,
                right: false,
                down: false,
                left: false
            });
        } else if(i === 63) {
            squaresArr.push({
                id: i,
                value: null,
                up: false,
                right: false,
                down: false,
                left: true
            });
        } else if(i === 77) {
            squaresArr.push({
                id: i,
                value: null,
                up: false,
                right: true,
                down: false,
                left: false
            });
        } else if(i === 139) {
            squaresArr.push({
                id: i,
                value: null,
                up: false,
                right: false,
                down: true,
                left: false
            });
        } else {
            squaresArr.push({
                id: i,
                value: null,
                up: false,
                right: false,
                down: false,
                left: false
            });
        }
    }

    return squaresArr;
}
